import skyRaty from "../../components/skyraty.vue";
export default({
	components:{
		skyRaty
	},
	data:function(){
		return {
			pageLoad:false,
			pageData:{},
			keyword:"",
			page:"product"
		}
	},
	onLoad:function(ops){
		this.keyword=ops.keyword;
		this.getPage();
	},
	methods:{
		goProduct:function(shopid,id){
			uni.navigateTo({
				url:"../b2b_product/show?shopid="+shopid+"&id="+id
			})
			 
		},
		goShop:function(shopid){
			
		},
		search:function(){
			this.getPage();
		},
		setPage:function(page){
			this.page=page;
			this.pageLoad=false;
			this.pageData={};
			this.getPage();
		},
		getPage:function(){
			if(this.page=="product"){
				this.getProduct();
			}else{
				this.getShop();
			}
		},
		getShop:function(){
			var that=this;
			this.app.get({
				url:that.app.apiHost+"/module.php?m=b2b_search&a=shop&ajax=1",
				data:{
					keyword:this.keyword
				},
				dataType:"json",
				success:function(res){
					that.pageLoad=true;
					that.pageData=res.data;
				}
			})
		},
		getProduct:function(){
			var that=this;
			this.app.get({
				url:that.app.apiHost+"/module.php?m=b2b_search&a=product&ajax=1",
				data:{
					keyword:this.keyword
				},
				dataType:"json",
				success:function(res){
					that.pageLoad=true;
					that.pageData=res.data;
				}
			})
		}
	}
});
